export default {
	data() {
		return {
			settime: null,
			msg: '', //留言
			name: '', //姓名
			phone: '', //电话
			idnumber: '', //要上传的身份证正面
			idnumberimg: '', //身份证预览图
			idnumberplane: '', //身份证副面
			idnumberplaneimg: '', //身份证副面预览图
			vehiclelicencedisc: '', //要上传的行车证正面
			vehiclelicencediscimg: '', //行车证正面预览图
			vehiclelicencediscplane: '', //要上传的行车证副面
			vehiclelicencediscplaneimg: '', //要上传的行车证副面预览图
		};
	},
	methods: {
		//获取手机号信息
		async getPhoneNumber(e) {
			let [err, code] = await uni.login({
				provider: 'weixin'
			})
		
			if (e.detail.encryptedData && e.detail.iv) {
				let res = await this.ajax(this.api.getPhone, {
					code: code.code,
					iv: e.detail.iv,
					encryptedData: e.detail.encryptedData
				}, 'POST')
				console.log(res)
				if (res.data.code == 200) {
					if (res.data.data != 'null') {
						console.log('获取手机号成功!')
						console.log(res.data.data)
						this.phone=res.data.data
					} else {
						uni.showToast({
							title: '获取手机号失败,请重试',
							duration: 2000,
							icon: 'none'
						})
					}
				}
		
			}
		},
		
		
		//提交
		async submit() {
			let {
				name,
				phone,
				msg,
				idnumber,
				idnumberplane,
				vehiclelicencedisc,
				vehiclelicencediscplane
			} = this
			name = name.trim()
			if (!name.length) {
				uni.showToast({
					title: '请输入联系人',
					duration: 2000,
					icon: 'none'
				})
				return false
			} else if (phone.length != 11) {
				uni.showToast({
					title: '请输入正确的联系电话',
					duration: 2000,
					icon: 'none'
				})
				return false
			} else if (!idnumber.length) {
				uni.showToast({
					title: '请输入上传身份证正面',
					duration: 2000,
					icon: 'none'
				})
				return false
			} else if (!idnumberplane.length) {
				uni.showToast({
					title: '请输入上传身份证副面',
					duration: 2000,
					icon: 'none'
				})
				return false
			} else if (!vehiclelicencedisc.length) {
				uni.showToast({
					title: '请输入上传行车证正面',
					duration: 2000,
					icon: 'none'
				})
				return false
			} else if (!vehiclelicencediscplane.length) {
				uni.showToast({
					title: '请输入上传行车证副面',
					duration: 2000,
					icon: 'none'
				})
				return false
			}
			//验证通过
			uni.showLoading({
				mask: true,
				title: '提交中...'
			})
			let carInsurance = await this.ajax(this.api.carInsurance, {
				msg,
				name,
				phone,
				idnumber,
				idnumberplane,
				vehiclelicencedisc,
				vehiclelicencediscplane
			}, 'POST')
			uni.hideLoading()
			if (carInsurance.data.code) {
				uni.showToast({
					title: carInsurance.data.msg,
					duration: 2000,
					icon: 'none',
					mask: true
				})
				if (carInsurance.data.code == 200) {
					this.settime = setTimeout(() => {
						uni.navigateBack({
							delta: 1
						})
					}, 2000)
				}
			}

		},


		// 预览示例
		previewPictures(i) {
			console.log(i)
			let img = ''
			switch (i) {
				case 1:
					img = getApp().globalData.example.idnumber
					break
				case 2:
					img = getApp().globalData.example.idnumberplane
					break
				case 3:
					img = getApp().globalData.example.driving_license_just
					break
				case 4:
					img = getApp().globalData.example.driving_license_vice
					break
			}

			let arr = [img]
			uni.previewImage({
				urls: arr
			})
		},


		// 上传图片
		async uploadfile(i) {
			let [error, chooseImage] = await uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从哪里选择图片 相册和相机

			})
			if (!error) {
				switch (i) {
					case 1:
						this.idnumberimg = chooseImage.tempFilePaths[0]
						break
					case 2:
						this.idnumberplaneimg = chooseImage.tempFilePaths[0]
						break
					case 3:
						this.vehiclelicencediscimg = chooseImage.tempFilePaths[0]
						break
					case 4:
						this.vehiclelicencediscplaneimg = chooseImage.tempFilePaths[0]
						break
				}


				// 上传图片
				let [err, uploadres] = await uni.uploadFile({
					url: this.api.uplode_img,
					filePath: chooseImage.tempFilePaths[0],
					name: 'img',
				})
				if (!err) {
					//转化为json
					let data = JSON.parse(uploadres.data)
					//要上传的行驶证正页
					switch (i) {
						case 1:
							this.idnumber = data.url
							break
						case 2:
							this.idnumberplane = data.url
							break
						case 3:
							this.vehiclelicencedisc = data.url
							break
						case 4:
							this.vehiclelicencediscplane = data.url
							break
					}

				} else {
					uni.showToast({
						title: '上传失败',
						duration: 2000,
						icon: 'none'
					})
				}
			}
		},


